._makesList {
  max-width: 800px;
  max-width: var(--max-column-width);
  margin: 0 auto;
}

._makes {
}

._makes--item {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  background: white;
  margin-bottom: 2px;
  padding: calc(var(--spacing) / 2) calc(var(--spacing) * 1);
  gap: calc(var(--spacing) * 1);
}
</style>
